<template>
    <div class="page-container">
        <el-main>
            <el-row><div class="park-title">{{ companyname }}</div></el-row>
            <el-row>
                <div class="park-list">
                    <template v-for="(item,index) in dataList">
                        <div class="park-item" :key="index" @click="click(item.id)">
                            <div class="flex flex-align-start flex-pack-justify">
                                <div>
                                    <div class="park-name">{{item.parkname}}</div>
                                    <div class="park-info flex flex-align-start">
                                        <div class="m-r-30">
                                            <h6>种植面积</h6>
                                            <div class="flex flex-align-end">
                                                <strong>{{item.parkarea}}</strong>
                                                <span>亩</span>
                                            </div>
                                        </div>
                                        <div>
                                            <h6>作物</h6>
                                            <p>{{item.parkPlant}}</p>
                                        </div>
                                    </div>
                                    <div class="park-addr flex flex-align-center">
                                        <i class="el-icon-location-outline m-r-10"></i>
                                        <p>{{item.parkaddrs}}</p>
                                    </div>
                                </div>
                                <div>

                                </div>
                                <div class="text-right">
                                    <div class="park-btn">
                                        <el-button size="small" @click.stop="edit(item)">园区信息</el-button>
                                        <el-button size="small" @click.stop="remove(index)">删除</el-button>
                                    </div>
                                    <div class="m-t-15 park-plot flex flex-align-center">
                                        <span class="m-r-15">下属地块</span>
                                        <el-tag size="mini" effect="dark" type="primary">{{item.plotNum}}</el-tag>
                                    </div>
                                    <div class="park-dev m-t-15 flex flex-align-center">
                                        <div class="text-center m-r-30">
                                            <i class="el-icon-bell"></i>
                                            <p>传感预警</p>
                                            <h5>{{item.warningNum}}</h5>
                                        </div>
                                        <div class="text-center m-r-30">
                                            <i class="el-icon-monitor"></i>
                                            <p>在线设备</p>
                                            <h5>{{item.deviceOn}}</h5>
                                        </div>
                                        <div class="text-center">
                                            <i class="el-icon-monitor" style="color:#909399"></i>
                                            <p>离线设备</p>
                                            <h5>{{item.deviceOff}}</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </template>
                    <div class="park-item" @click="add">
                        <div class="add-item text-center">
                            <i class="el-icon-circle-plus-outline"></i>
                            <p>新建园区</p>
                        </div>
                    </div>
                </div>
            </el-row>
            <el-dialog title="园区信息" :visible.sync="dialogVisible" width="1000px" :close-on-click-modal="false">
                <el-form ref="form" size="small" :rules="rules" :model="form" label-width="150px">
                    <el-tabs tab-position="left">
                        <el-tab-pane label="园区资料">
                            <el-form-item label="园区名称" prop="parkname">
                                <el-input style="width:450px;" v-model="form.parkname" placeholder="请填写园区名称"></el-input>
                            </el-form-item>
                            <el-form-item label="种植规模" prop="parkarea">
                                <el-input style="width:260px;" v-model="form.parkarea" placeholder="请填写面积">
                                    <template slot="append">亩</template>
                                </el-input>
                            </el-form-item>
                            <el-form-item label="园区地址" prop="parkaddr">
                                <el-select style="width:150px;" v-model="form.province" placeholder="请选择省" @change="chooseProvince">
                                    <el-option
                                        v-for="item in provinceData"
                                        :key="item.code"
                                        :label="item.name"
                                        :value="item.name">
                                    </el-option>
                                </el-select>
                                <el-select style="width:150px;" v-model="form.city" placeholder="请选择市" @change="chooseCity">
                                    <el-option
                                        v-for="item in cityData"
                                        :key="item.code"
                                        :label="item.name"
                                        :value="item.name">
                                    </el-option>
                                </el-select>
                                <el-select style="width:150px;" v-model="form.area" @change="chooseArea" placeholder="请选择区、县">
                                    <el-option
                                        v-for="item in areaData"
                                        :key="item.code"
                                        :label="item.name"
                                        :value="item.name">
                                    </el-option>
                                </el-select>
                                <div class="flex flex-align-center m-t-15">
                                    <el-input style="width:450px;" class="m-r-10" v-model="form.parkaddr" placeholder="请填写详细的街道信息"></el-input>
                                    <i class="el-icon-location" style="font-size: 24px;" @click="positionAddr"></i>
                                </div>
                            </el-form-item>
                            <el-form-item label="联系人" prop="contacts">
                                <el-input style="width:260px;" v-model="form.contacts"></el-input>
                            </el-form-item>
                            <el-form-item label="联系电话" prop="contacttel">
                                <el-input style="width:260px;" v-model="form.contacttel"></el-input>
                            </el-form-item>
                            <el-form-item label="平面图类型" prop="pictype">
                                <el-radio-group v-model="form.pictype">
                                    <el-radio :label="1">使用谷歌地图</el-radio>
                                    <el-radio :label="2">使用我上传的平面图</el-radio>
                                </el-radio-group>
                            </el-form-item>
                            <div v-if="form.pictype == 1">
                                <GmapMap :center="form.center" :zoom="form.zoom" map-type-id="satellite" style="width: 100%; height: 300px"></GmapMap>
                            </div>
                            <div v-if="form.pictype == 2" class="text-center">
                                <el-upload
                                    action="https://jsonplaceholder.typicode.com/posts/"
                                    :file-list="form.picSrc"
                                    :show-file-list="false">
                                    <div slot="file" slot-scope="{file}">
                                        <img style="width: 100%;height:300px;" :src="file.url" alt="">
                                    </div>
                                    <el-button size="small" type="primary">点击上传</el-button>
                                </el-upload>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="设备列表">
                            <div class="device-list">
                                <template v-for="(item,index) in deviceList">
                                    <div class="device-item flex flex-align-center flex-pack-justify" :key="index">
                                        <div class="flex flex-align-center">
                                            <i :class="item.deviceIcon"></i>
                                            <span>{{ item.deviceName }}</span>
                                        </div>
                                        <span>{{ item.deviceNum }}</span>
                                    </div>
                                </template>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button size="small" @click="dialogVisible = false">取消</el-button>
                    <el-button size="small" type="primary" @click="save">保存</el-button>
                </span>
            </el-dialog>
        </el-main>
    </div>
</template>

<script>
import Vue from 'vue'
import { search, add, edit } from '@/api/park'
import qs from 'qs'
import axios from 'axios'
import store from '@/store'

import * as VueGoogleMaps from 'vue2-google-maps'
Vue.use(VueGoogleMaps, {
    load: {
      key: 'AIzaSyAkQyi3T57OUym-z7623d6PmLGX8ZWGAmE',
      v: '3.26',
    },
    loadCn:true,
    installComponents: true,
});

export default {
    data(){
        return{
            companyid: 0,
            companyname: '',
            dataList:[],
            isAdd: true,
            dialogVisible: false,
            form: {
                parkname: '',
                parkarea: '',
                province:'',
                city:'',
                area:'',
                parkaddr: '',
                contacts: '',
                contacttel: '',
                pictype: 1,
                zoom: 16,
                lat: 45.253458,
                lng: 131.120272,
                center: {lat:45.253458, lng: 131.120272},
                picSrc: [],
            },
            provinceData:[],
            cityData:[],
            areaData:[],
            rules: {
                parkname: [
                    { required: true, message: '请填写园区名称', trigger: 'blur' },
                ],
                parkarea: [
                    { required: true, message: '请填写种植规模', trigger: 'blur' },
                ],
                parkaddr: [
                    { required: true, message: '请填写园区地址', trigger: 'blur' },
                ],
                pictype: [
                    { required: true, message: '请选择平面图类型', trigger: 'change' },
                ]
            },

            // 设备列表
            deviceList: [
                {
                    deviceIcon: 'el-icon-sunny',
                    deviceName: '空气温度传感器',
                    deviceNum: 4
                }
            ]
        }
    },
    methods: {
        initData() {
            this.companyid = this.$store.state.user.company
            this.doSearch(qs.stringify({companyid: this.companyid}))

            /* if (!isNaN(Number(this.$route.params.id))) {
                this.companyid = Number(this.$route.params.id)
                this.doSearch(qs.stringify({companyid: this.companyid}))
            } */
        },
        doSearch(data){
            search(data).then(res => {
                if(res.code == 1){
                    this.companyname = res.data.companyname
                    this.dataList = res.data.data
                    this.dataList.forEach(item => {
                        item.parkaddrs = item.province + " " + item.city + " " + item.parkaddr
                    });
                }else {
                    this.$message.error(res.msg);
                }
            }).catch(err => {
                this.$message.error(err);
            })
        },
        add(){
            this.isAdd = true
            this.dialogVisible = true
            this.resetForm()
            this.initProvince()
        },
        edit(row){
            this.isAdd = false
            this.dialogVisible = true
            this.resetForm()
            this.initProvince()
            this.form = row
        },
        remove(index){
            this.$confirm('此操作将删除该园区下所有的地块, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
            }).catch(() => {

            })
        },
        save(){
            this.$refs['form'].validate((valid) => {
                if (valid) {
                    this.form.companyid = this.companyid
                    if(this.isAdd){
                        add(qs.stringify(this.form)).then(res => {
                            if(res.code == 1){
                                this.$message({
                                    message: res.msg,
                                    type: 'success'
                                });
                                this.doSearch(qs.stringify({companyid: this.companyid}))
                                this.dialogVisible = false
                            }else{
                                this.$message.error(res.msg);
                            }
                        }).catch(err => {
                            this.$message.error(err);
                        })
                    }else{
                        edit(qs.stringify(this.form)).then(res => {
                            if(res.code == 1){
                                this.$message({
                                    message: res.msg,
                                    type: 'success'
                                });
                                this.doSearch(qs.stringify({companyid: this.companyid}))
                                this.dialogVisible = false
                            }else{
                                this.$message.error(res.msg);
                            }
                        }).catch(err => {
                            this.$message.error(err);
                        })
                    }

                } else {
                    this.$message.error("请检查您的输入！");
                    return false;
                }
            });
        },
        resetForm() {
            this.form = {
                parkname: '',
                parkarea: '',
                province:'',
                city:'',
                area:'',
                parkaddr: '',
                contacts: '',
                contacttel: '',
                pictype: 1,
                zoom: 16,
                lat: 45.253458,
                lng: 131.120272,
                center: {lat:45.253458, lng: 131.120272},
                picSrc: [],
            }
        },
        initProvince(){
            axios.get('../../../static/json/area.json').then(res=>{
                this.provinceData = res.data;
            }).catch(e => {
                this.$message.error("网络连接超时");
            })
        },
        chooseProvince(value){
            this.form.city = '';
            this.form.area = '';
            this.cityData = [];
            this.areaData = [];
            this.provinceData.map(e=>{//遍历数据
                if( value == e.name){
                    this.cityData = e.cityList;
                    return;
                }
            })
        },
        chooseCity(value){
            this.form.area = '';
            this.cityData.map(e=>{//遍历数据
                if( value == e.name){
                    this.areaData = e.areaList;
                    return;
                }
            })
        },
        chooseArea(){

        },
        positionAddr(){
            /* let self = this
            let address = self.form.province + self.form.city + self.form.area + self.form.parkaddr
            self.form.center = {lat: address.geometry.location.lat(), lng: address.geometry.location.lng()} */
        },
        click(id){
            this.$router.push('/plot/list/'+ id)
        }
    },
    created(){
        store.dispatch('GetInfo').then(() => {})
        this.initData()
    }
}
</script>

<style>

</style>



