import request from '@/utils/request'

export function search(data){
    return request({
        url: 'park/list',
        method: 'post',
        data
    })
}

export function add(data){
    return request({
        url: 'park/add',
        method: 'post',
        data
    })
}

export function edit(data){
    return request({
        url: 'park/edit',
        method: 'post',
        data
    })
}


